.order {
    padding-top: 48px;
    height: 100%;
    &__title {
        margin: 0;
        padding-left: 43px;
        font-weight: 700;
        font-size: 35px;
        color: #333333;
    }
    &__wrapper {
        margin-top: 20px;
        padding: 0;
        display: flex;
        background: #FFFFFF;
        border: 1px solid #C5C5C5;
        border-radius: 4px;
    }
    &__delivery {
        max-width: 1117px;
        width: 100%;
        padding: 40px 82px 40px 43px;
        border-right: 1px solid #8C8C8C;
    }

    &__info {
        padding: 40px 86px 40px 50px;
        max-width: 625px;
        width: 100%;
        &-title {
            span {
                font-weight: 400;
            }
        }
        &-wrapper {
            margin-top: 30px;
            padding-bottom: 14px;
            border-bottom: 1px solid #D6D6D6;
        }
        &-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            &:not(:last-child) {
                margin-bottom: 14px;
            }
        }
        .summary,
        .discount,
        .prediscount {
            &__text {
                font-weight: 400;
                font-size: 22px;
                color: #333333;
            }
            &__count {
                font-weight: 700;
                font-size: 22px;
                color: #333333;
            }
        }
    }
}

.delivery {
    &__choice-place {
        margin-top: 14px;
    }
}

.slider {
    margin-top: 14px;
    display: flex;
    align-items: center;
    &__item {
        max-width: 490px;
        height: 152px;
        display: flex;
        align-items: center;
        cursor: pointer;
        &-address {
            max-width: 360px;
        }
        &-price {
            font-weight: 400;
            font-size: 22px;
            color: #333333;
        }
        &:not(:last-child) {
            margin-right: 14px;
        }
    }
}

.date {
    margin-top: 30px;
    &__wrapper {
        display: flex;
        align-items: center;
    }
    &__item {
        max-width: 272px;
        cursor: pointer;
        &:not(:last-child) {
            margin-right: 14px;
        }
    }
    &__other {
        margin-left: 15px;
    }
    &__options {
        margin-top: 14px;
    }

    /* Checkbox */
    .check {
        &__input {
            appearance: none;
            position: absolute;
            &:checked + .check__box:after {
                opacity: 1;
            }
            &:disabled + .check__box {
                background-color: rgba(#065CC2, 0.25);
            }
        }
        &__label {
            padding-left: 35px;
            font-weight: 400;
            font-size: 22px;
            color: #333333;
            cursor: pointer;
        }
        &__box {
            margin-left: -35px;
            position: absolute;
            width: 24px;
            height: 24px;
            background-color: #065CC2;
            border-radius: 2px;
            
            &:after {
                content: '';
                position: absolute;
                top: 4px;
                left: 2px;
                display: block;
                opacity: 0;
                width: 24px;
                height: 24px;
                transition: opacity 0.3s;
                background-image: url('../img/confirm.svg');
                background-repeat: no-repeat;
            }
        }
        &:not(:last-child) {
            margin-bottom: 14px;
        }
    }
}

.recipient {
    margin-top: 30px;
    position: relative;
    &__info {
        margin-top: 14px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        &-select {
            &:not(:last-child) {
                margin-right: 14px;
            }
        }
    }

    /* Select */
    .select {
        position: relative;
        &__descr {
            position: absolute;
            top: 8px;
            left: 14px;
            font-weight: 400;
            font-size: 17px;
            color: #333333;
        }
        select {
            padding: 34px 58px 12px 14px;
            background: #FFFFFF;
            border: 1px solid #D6D6D6;
            border-radius: 4px;
            appearance: none;

            font-family: 'PT Root UI', sans-serif;
            font-weight: 700;
            font-size: 22px;
            color: #333333;
        }
        &:after {
            content: '';
            position: absolute;
            right: 19px;
            top: 50%;
            width: 19px;
            height: 19px;
            background-image: url('../img/arrow_down.svg');
            background-repeat: no-repeat;
            pointer-events: none;
            z-index: 1;
        }
        &__new-user,
        &__new-phone {
            margin-top: 14px;
        }
    }
}

.bottom {
    position: sticky;
    bottom: 0;
    margin-top: 20px;
    width: 100%;
    min-height: 120px;
    border-top: 1px solid #C5C5C5;
    background-color: #F8F8F8;
    z-index: 99;
    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
        padding: 0 43px;
    }
    &__info {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 489px;
        width: 100%;
        &-descr {
            font-weight: 400;
            font-size: 22px;
            color: #333333;
            margin-right: 70px;
        }
        &-mobile {
            display: none;
        }
        &-price {
            font-weight: 700;
            font-size: 35px;
            color: #333333;
        }
        &-link {
            display: none !important;
        }
    }
    &__btn {
        padding: 21px 50px;
        background: linear-gradient(0deg, #EC0002, #EC0002), linear-gradient(91.91deg, rgba(208, 38, 38, 0) 0%, #D02626 99.65%), #FF0000;
        border: 1px solid #E01D1D;
        box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.12), 0px 3px 1px rgba(0, 0, 0, 0.04), 0px 2px 0px rgba(149, 135, 128, 0.4);
        border-radius: 4px;

        font-weight: 700;
        font-size: 29px;

        color: #FFFFFF;
        cursor: pointer;
    }
}


.title {
    &__h2 {
        margin: 0;
        font-weight: 700;
        font-size: 29px;
        color: #333333;
    }
    &__h3 {
        margin: 0;
        font-weight: 700;
        font-size: 22px;
        color: #333333;
    }
}
.descr {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    color: #1A1A1A;
}
.link {
    display: inline-block;
    font-weight: 400;
    font-size: 22px;
    color: #065CC2;
    transition: transform 0.4s;
    &:hover {
        transform: scale(1.03);
    }
}
.item {
    padding: 20px;
    border: 2px solid #D6D6D6;
    border-radius: 4px;
    &.active {
        border: 2px solid #065CC2;
    }
}